<template>
  <div class="profile">
    <Navbar />
    <h1 class="profile-title text-center m-4">
      Профиль
    </h1>
    <div class="container mt-3">
      <div class="d-flex flex-row">
        <div class="col-xl-2 col-lg-2 col-md-3 d-none d-md-block mr-5" />
        <div class="col-xl-7 col-lg-7 col-md-9 col-sm-12 col-12 mr-5 border">
          <div class="media m-3">
            <img
              class="mr-3 align-items-center mt-3"
              src="@/img/reviews-img.png"
              alt="..."
            >
            <div class="media-body align-items-center">
              <h5
                class="mt-0 mb-0"
                style="margin-left: 12px;"
              >
                {{ user1.name }}
              </h5>
              <div
                class="account d-flex align-items-center justify-content-start p-2"
              >
                <img
                  class="img-account"
                  src="../img/account-img/account-cart.svg"
                  alt="cart"
                >
                <router-link to="/basket">
                  <h4>
                    <a href="#">Корзина</a>
                  </h4>
                </router-link>
              </div>
              <div
                class="account d-flex align-items-center justify-content-start p-2"
              >
                <img
                  class="img-account"
                  src="@/img/favorite.svg"
                  alt="FAVOURITE"
                >
                <router-link to="/favorite">
                  <h4>
                    <a href="#">Список желаний</a>
                  </h4>
                </router-link>
              </div>
            </div>
          </div>
          <b-form
            class="p-3"
            @submit="apdateUser"
          >
            <b-form-group
              label="Имя:"
              label-for="input-1"
            >
              <b-form-input
                v-model="user1.name"
                type="text"
                required
              />
            </b-form-group>
            <b-form-group
              label="Фамилия:"
              label-for="input-1"
            >
              <b-form-input
                v-model="user1.last_name"
                type="text"
                required
              />
            </b-form-group>
            <b-form-group
              label="Отчество:"
              label-for="input-1"
            >
              <b-form-input
                v-model="user1.fathers"
                type="text"
                required
              />
            </b-form-group>
            <b-form-group
              label="Email:"
              label-for="input-2"
            >
              <b-form-input
                id="input-2"
                v-model="user1.email"
                type="email"
                required
              />
            </b-form-group>
            <b-form-group
              label="Город:"
              label-for="input-6"
            >
              <b-form-input
                v-model="user1.city"
                type="text"
              />
            </b-form-group>
            <div class="text-right">
              <button
                type="submit"
                class="help-b btn btn-success ml-2"
              >
                Сохранить
              </button>
            </div>
          </b-form>
        </div>
        <div class="col-xl-2 col-lg-2 d-none d-lg-block" />
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import axios from "axios";
import Navbar from "./Navbar.vue";
import Footer from "./Footer.vue";
export default {
    name: "Prof",
    components: {
        Navbar,
        Footer
    },
    data() {
        return {
            user1: {},
            config: {
                headers: {
                    Authorization: "Token " + this.$cookies.get("user_session")
                }
            },
            show: true
        };
    },
    mounted() {
        axios
            .get("http://bohmann.shop/api/user/", this.config)
            .then(response => (this.user1 = response.data));
    },
    methods: {
        apdateUser() {
            axios.put(`http://bohmann.shop/api/users/${this.user1.id}/`, {
                name: this.user1.name,
                username: this.user1.username,
                password: this.user1.password,
                last_name: this.user1.last_name,
                fathers: this.user1.fathers,
                city: this.user1.city
            });
        }
    }
};
</script>
<style>
.form-control[readonly] {
  background-color: #fff;
  opacity: 1;
}
</style>
